import * as React from 'react';
import { Link } from 'react-router-dom';
import logo from '../img/logo.svg';

export const SimpleHeader: React.SFC<{}> = () => {
  return (
    <Link className="logo" to="/">
      <img src={logo} alt="logo" />
    </Link>
  );
};



